export default "window.knimeLoader = function () {\n  var origin = '%ORIGIN%';\n  var namespace = '%NAMESPACE%';\n  var nodeId = '%NODEID%';\n  var knimeLoaderCount = '%LIBCOUNT%';\n\n  if (knimeLoaderCount === 0) {\n    parent.postMessage({\n      nodeId: nodeId,\n      type: 'load'\n    }, origin);\n  }\n\n  return function knimeLoader(success) {\n    knimeLoaderCount--;\n\n    if (!success) {\n      parent.postMessage({\n        nodeId: nodeId,\n        type: 'error'\n      }, origin);\n      throw new Error('Script could not be loaded');\n    }\n\n    if (knimeLoaderCount === 0) {\n      var view = window[namespace];\n\n      if (!view) {\n        parent.postMessage({\n          nodeId: nodeId,\n          type: 'error'\n        }, origin);\n        throw new ReferenceError('no view found under namespace ' + namespace);\n      }\n\n      parent.postMessage({\n        nodeId: nodeId,\n        type: 'load'\n      }, origin);\n    }\n  };\n}();"